@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.titleContainer {
    
}

.titleContainer > h1 {
    font-family: 'Langar';
    font-size: 80px;
    margin-bottom: 20px;
    margin-top: 0;
}

@media only screen and (max-width: 768px) {
    .titleContainer > h1 {
        font-family: 'Langar';
        font-size: 55px;
        margin-bottom: 10px;
        margin-top: 0;
    }
  }


.sectionButton {
    cursor: pointer;
}

.sectionButton a:-webkit-any-link {
    color: black;
    text-decoration: none;
    cursor: pointer;
    -webkit-text-decoration-color: rgb(69, 123, 157);
            text-decoration-color: rgb(69, 123, 157);
}

.sectionButton a:any-link {
    color: black;
    text-decoration: none;
    cursor: pointer;
    -webkit-text-decoration-color: rgb(69, 123, 157);
            text-decoration-color: rgb(69, 123, 157);
}
.sectionsContainer {
  padding-top: 7vh;
  text-decoration: underline;
  text-underline-offset: 7px;
  -webkit-text-decoration-color: rgb(69, 123, 157);
          text-decoration-color: rgb(69, 123, 157);
}
.pageTitle {
  font-size: large;
  text-decoration: underline;
  text-underline-offset: 10px;
  -webkit-text-decoration-color: rgb(69, 123, 157);
          text-decoration-color: rgb(69, 123, 157);
}

.cardContainer {
  padding-bottom: 10px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.title {
  font-size: x-large;
  margin-bottom: 0;
  margin-top: 0;
}

.subTitle {
  font-size: large;
  margin-top: 0;
  margin-bottom: 0;
}

.subTitle2 {
  font-size: small;
  margin-top: 0;
}


.cardContainer li {
  padding-bottom: 10px;
  font-size: 16px;
}

.backButton {
  width: 30px;
  height: 30px;
  
}

.buttonContainer {
  display: flex;
  align-items: center;

  cursor: pointer;

  margin-top: 0;
  margin-bottom: 0;

  position: fixed;
  width: 100%;
  top: 3vh;
}

.buttonContainer a:-webkit-any-link {
  color: black;
  text-decoration: none;
  cursor: pointer;
  -webkit-text-decoration-color: rgb(69, 123, 157);
          text-decoration-color: rgb(69, 123, 157);
  font-size: 10px;
}

.buttonContainer a:any-link {
  color: black;
  text-decoration: none;
  cursor: pointer;
  -webkit-text-decoration-color: rgb(69, 123, 157);
          text-decoration-color: rgb(69, 123, 157);
  font-size: 10px;
}

.buttonContainer h1 {
  margin-top: 0;
  margin-bottom: 0;
}
body {
    padding-left: 10vw;
    padding-right: 10vw;
    padding-top: 10vh;
    font-family: 'Langar';
}

@media only screen and (max-width: 768px) {
  body {
      padding-top: 5vh;
  }
}
